// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import vueresource from "vue-resource"

Vue.config.productionTip = false
Vue.use(vueresource)

//自定义的指令
//directive指令; <美>命令，训令，指令; 方针; 指示性的，指导性的，指挥的; 管理的; 指向[定向]式的
//bind 绑定的
//el
//binding 绑定
//vnode 虚拟节点


//Math马思; 数学（等于mathematics）
//random 随机
//toString
//slice切成片; 切下; 划分; 薄片; 一部分; 曲线球; 斜击

//全局的指令
//Vue.directive("rainbow",{
//	bind(el,binding,vnode){
//		el.style.color="#"+Math.random().toString(16).slice(2,8)
//	}
//})

Vue.directive("theme",{
	bind(el,binding,vnode){
		if(binding.value=="1260px"){
			el.style.maxWidth="1260px";
		}else if(binding.value=="narrow"){
			el.style.maxWidth="560px";
		}
		if(binding.arg=="column"){
			el.style.background="#f4f5f5";
		}
	}
})
//制定一个过滤器
//Vue.filter("to-uppercase",function(value){
//	return value.toUpperCase()
//})
Vue.filter("sinppet",function(value){
	return value.slice(0,170)+"..."
})


/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



