<template>
  <div class="body" v-theme:column="'wide'">
    
		
		<div class="content">
		
			<div class="content_box">
				<div class="content_left">
					<div class="content_left_nav">
						<div class="c_nav_left">
							<h4 style="padding: 10px 0;">热门文章</h4>
							<input type="" class="search" placeholder="搜索" v-model="search"/>
						</div>
						<div class="c_nav_right">
							<a href="" class="lan">推荐</a>
							<a href="">前端</a>
							<a href="">后端</a>
							<a href="">ios</a>
							<a href="">android</a>
							<a href="">人工智能</a>
						</div>
						<div style="clear: both;">
							
						</div>
					</div>
					
					<div class="entry-list" v-for="blog in filter" :key="blog.title">
						<div class="entry_item">
							<div class="e_item_title">
								<router-link v-bind:to="'/blog/'+blog.id"><h4 v-rainbow>{{blog.title | to-uppercase}}</h4></router-link>
								<p>{{blog.body | sinppet}}</p>
							</div>
							<div class="e_item_mate">
								<a class="lan">前端</a>
								<a>有赞技术</a>
								<p>3小时前</p>
								<p>375次阅读</p>
							</div>
						</div>
					</div>
					
				</div>
				<div class="content_right">
					
				</div>
				<div class="clear_both"></div>
			</div>
		</div>		
	
  </div>
</template>

<script>
export default {
	//https://jsonplaceholder.typicode.com/posts
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      blogs:[],
      search:""
    }
  },
  created(){
  	this.$http.get("https://wd8893753510htkmbu.wilddogio.com/posts.json")
  	.then(function(data){
  		console.log(data)
//		this.blogs=data.body.slice(0,10)
//		console.log(this.blogs)
      return data.json()
  	}).then(function(data){
  		var blosarray=[];
  		for(let key in data){
  			data[key].id=key;
  			blosarray.push(data[key])
  		}
  		
  		
  		 this.blogs=blosarray;
  	})
  },
  computed:{
  	filter:function(){
  		return this.blogs.filter((blog)=>{
  			return blog.title.match(this.search);
  		})
  	}
  },
  //局部的过滤器
  filters:{
  	toUppercase(value){
  		return value.toUpperCase()
  	}
  },
  //局部的指令
  directives:{
  	"rainbow":{
  		bind(el,binding,vnode){
  			el.style.color="#333"
//			el.style.color="#"+Math.random().toString(16).slice(2,8)
  		}
  	}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.body{margin: 0; padding: 0; margin-top: 24px; font-family: "微软雅黑";background: #f4f5f5;}
			.clear_both{ clear: both;}
			/*content*/
			.content{margin-top: 24px;}
			.content_box{width: 960px; margin-left: auto;margin-right: auto;}
			.content_left{min-height: 500px; width: 680px;padding-left:20px; background: #FFFFFF; float: left;}
			.content_right{min-height: 500px; width: 240px;background: #FFFFFF; float: right;}
			.entry-list{border-bottom:solid 1px #E3E3E3; margin-bottom: 20px; padding-bottom: 20px;}
			.e_item_mate *{ display: inline; font-size: 13px; color: #888;}
			.c_nav_right{float: right; margin-top: 15px; }
			.c_nav_right a{ margin-right: 20px; color: #999;}
			.lan{color: #007fff !important;}
			.c_nav_left{float: left;}
			.c_nav_left h4{font-weight: normal;}
			.e_item_title h4{font-weight: 700;font-size: 17px;}
</style>



