<template>
	
	<div class="add-blog">
		<form v-if="!sub">
			<label >添加博客</label>
			<input type="text"  v-model="blog.title"/>
			
			<label >博客内容</label>
			<textarea v-model="blog.content"></textarea>
			<div id="checkboxes">
				<label >vue.js</label>
				<input type="checkbox"  value="vue.js" v-model="blog.categories"/>
				<label >node.js</label>
				<input type="checkbox"  value="node.js" v-model="blog.categories"/>
				<label >rect.js</label>
				<input type="checkbox"  value="rect.js" v-model="blog.categories"/>
				<label >anglar4.js</label>
				<input type="checkbox"  value="anglar4.js" v-model="blog.categories"/>
				<select  v-model="blog.author">
					<option v-for="author in authors" >
						{{author}}
					</option>
				</select>
				<button v-on:click.prevent="post">提交</button>
				
			</div>
			<hr />
			
			<div class="preview">
				<h3>博客总览</h3>
				<p>博客标题：{{blog.title}}</p>
				<p>博客内容：{{blog.content}}</p>
				
				<ul >
					<li v-for="categorie in blog.categories">
						{{categorie}}
					</li>
				</ul>
				<label >作者：{{blog.author}}</label>
				
				
			</div>
		</form>
		<!--true 显示-->
		<div v-if="sub">
					<h3>您的博客发布成功！</h3>
		</div>
	</div>
	
	
	
	
</template>

<script>
export default {
	//https://jsonplaceholder.typicode.com/
	//https://jsonplaceholder.typicode.com/posts
  name: 'add-blog',
  data () {
    return {
      sub:false,
      blog:{
      	title:'',
      	content:"",
      	author:"",
      	categories:[]
      	
      },
      authors:["wo","ni","ta"]
    }
  },
  methods:{
  		post:function(){
  			this.$http.post("https://wd8893753510htkmbu.wilddogio.com/posts.json",{
  				title:this.blog.title,
  				body:this.blog.content,
  				author:this.blog.author,
  				categories:this.blog.categories,
  				userid:1
  			}).then(function(data){
  				console.log(data);
  				console.log(this.sub);
  				this.sub=true;
  				console.log(this.sub);
  			})
  			
  			
  		}
  	}
  
}
</script>

<style scoped>
.add-blog *{box-sizing:border-box;}	
.add-blog {margin:20px auto; max-width: 600px; padding: 20px;}
label{display: block; margin: 20px 010px;}
input[type="text"],textarea,select{
	display: block;
	width: 100%;
	padding:8px;
}
textarea{height: 200px;}
#checkboxes label{
	display: inline-block;
	margin-top: 0;;
}
#checkboxes input{
	display: inline-block;
	margin-right: 10px;;
}
button{display: block;
margin: 20px 0;
background: crimson;
color: #FFFFFF;
border:0; padding: 14px; border-radius: 4px;font-size: 18px ;cursor: pointer;}

</style>


