<template>
<div id="blog">
	<h3>{{blog.title}}</h3>
	<p> {{blog.body}}</p>
</div>

</template>

<script>
	export default {
	  name: 'blog',
	  data(){
	  	return{
	  		id:this.$route.params.id,
	  		blog:{}
	  	}
	  },
	  created(){
	  	this.$http.get("https://wd8893753510htkmbu.wilddogio.com/posts/"+this.id+".json")
	  	.then(function(data){
	  		console.log(data);
	  		this.blog=data.body;
		  })
		}
}
</script>

<style>
	#blog{width: 800px; margin: 0 auto;}
	#blog h3{ margin-bottom: 35px;}
	
</style>


