import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import addblog from "@/components/addblog"
import blog from "@/components/blog"

Vue.use(Router)

export default new Router({
  mode:"history",
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/addblog',
      name: 'addblog',
      component: addblog
    },
    {
      path: '/blog/:id',
      name: 'addblog',
      component: blog
    }
  ]
})



